<template>
  <div class="ShopPageContainer">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1"><i class="el-icon-s-home"></i></el-menu-item>
        <el-menu-item index="2"><i class="el-icon-user-solid"></i></el-menu-item>
        <el-menu-item index="6" class="right-menu-2">Log Out</el-menu-item>
      </el-menu>
      <div class='player'>
        <video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions">
        </video-player>
      </div>
  </div>
</template>
<script>
//import { videoPlayer } from 'vue-video-player'

export default {
    name: 'PlayPage',
/*
    components: {
        videoPlayer
    }
    */
  data() {
      return {
        activeIndex: '1',
        playerOptions: {
              playbackRates: [0.5, 1.0, 1.5, 2.0], // 可选的播放速度
              autoplay: false, // 如果为true,浏览器准备好时开始回放。
              muted: false, // 默认情况下将会消除任何音频。
              loop: false, // 是否视频一结束就重新开始。
              preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
              aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
              fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
              sources: [{
                  type: "video/mp4", // 类型
                  src: require('../assets/presenter.mp4') // url地址
              }],
              //poster: require('../assets/presentation.jpeg'), // 封面地址
              notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
              controlBar: {
                  timeDivider: true, // 当前时间和持续时间的分隔符
                  durationDisplay: true, // 显示持续时间
                  remainingTimeDisplay: false, // 是否显示剩余时间功能
                  fullscreenToggle: true // 是否显示全屏按钮
              }
        }
    }
    },
    methods: {
        handleSelect(key) {
            if (key === '1') {
                this.onClickHome()
            } else if (key === '2') {
                this.onClickProfile('info','1')
            } else {
                this.onClickLogout()
            }
        },
        onClickHome() {
            this.$router.push('/');
        },
        onClickProfile() {
            this.$router.push('profile')
        },
        onClickLogout() {
            this.$store.dispatch('user/logout')
            .then(() => {
                this.$router.push('/')
            })
            .catch(() => {
                console.log("logout failure")
            })
        },
    }
} 
</script>
<style scoped>
    .player {
        height: 80%;
        width: 70%;
        margin: auto;
        padding-top: 5%;
    }
</style>